#nav {position: fixed; top: 0; z-index: 100; padding: 0 5%; width: 100%; height: 10%; background-color: rgba(255, 255, 255, 0.3); backdrop-filter: blur(5px); display: flex; align-items: center; transition: all .5s;}
#nav.shadow {box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.1), 0 8px 16px -8px rgba(0, 0, 0, 0.15), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);}
#nav > a {font-weight: 400; font-size: 18px; color: #2c2c2c; text-transform: uppercase; padding: 1% 2%;}
#nav > a:hover {font-weight: 500;}
.logo_wrap {perspective: 50px; transform-style: preserve-3d; margin-right: 2%;}
.logo {font-weight: 700; font-size: 30px; color: darkorange; letter-spacing: -2px; transform: rotateY(25deg);}