.movie {
    position: relative;
    /* background-color: rgba(255, 255, 255, 0.9); */
    background-color: rgba(239, 243, 247, 0.95);
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 10px;
    color: #adaeb9;
    /* color: #828282; */
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.1),
      0 8px 16px -8px rgba(0, 0, 0, 0.15), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transition: all .5s;
}

/* .movies .movie::after {content: ""; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.8);} */

.movies .movie:hover {
    box-shadow: 0 20px 40px -5px rgba(50, 50, 93, 0.1),
      0 15px 30px -8px rgba(0, 0, 0, 0.15), 0 -10px 20px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
    /* display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px; */
    text-decoration: none;
    color: inherit;
}

.movie_contents {
    display: flex;
    align-items: flex-start;
}

.movie_bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 11px;
}

.movie img {
    position: relative;
    z-index: 10;
    /* top: -50px; */
    max-width: 150px;
    width: 100%;
    height: auto !important;
    margin-right: 70px;
    transition: transform .5s;
    /* box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025); */
}

.movie:hover img {
    transform: translateY(-50px);
}

.movie:hover .img_shadow {
    top: 50px;
    opacity: 0.6;
}

.movie .img_shadow {
    position: absolute;
    width: 100%;
    height: auto;
    background-image: inherit;
    background-size: cover;
    border-radius: 5px;
    z-index: 0;
    filter: blur(12px);
    top: 20px;
    left: 23px;
    opacity: 0;
    transition: all .5s;
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}

@media screen and (max-width: 1090px) {
    .movie_contents {
        display: block;
        align-items: flex-start;
    }
}