.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.spin {margin-right: 10px; width: 40px; animation: spin_move 1s linear infinite;}

@keyframes spin_move {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.loader__text {color: #e5e5e5;}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

@media screen and (max-width: 1090px) {
    .movies {
        grid-template-columns: 1fr;
        width: 100%;
    }
}

